import cx from 'classnames';
import PropTypes from 'prop-types';
import { PureComponent } from 'react';
import {
    withTranslation,
    initTranslations,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import ChooseOptionWindow from './components/choose-option-window/ChooseOptionWindow';
import GenericMessage from './components/generic-message/GenericMessage';
import Hoop from './components/hoop/Hoop';
import VARIANTS_ICON from './components/generic-message/const/variantsConst';
// Services
import TwilioService from './services/TwilioService';
import ChatService from './services/ChatService';
// Utils
import CssImport from './utils/CssImport';
import GatewayHttpClient from './utils/GatewayHttpClient';
// Styles
import './styles/chat-module.scss';

initTranslations({
    defaultTranslations: {
        'en-US': () => import('./i18n/en-us.json'),
    },
    partnerId: 'walmart',
    shouldLoadStrings: false,
});
class ChatModule extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        containerElement: PropTypes.object,
        accountIdentifier: PropTypes.string,
        accessToken: PropTypes.string,
        brand: PropTypes.string,
        applicationId: PropTypes.string,
        env: PropTypes.string,
        onChatWidgetClose: PropTypes.func,
        i18n: PropTypes.object,
        isI18nReady: PropTypes.bool,
        prefixAPIUrl: PropTypes.string,
    };

    static defaultProps = { onChatWidgetClose: () => Promise.resolve() };

    state = {
        shouldShowChooseOptionWindow: false,
        shouldShowErrorPage: false,
        shouldShowHoopWindow: false,
    };

    render() {
        const { isDesignTokenApplied } = this.state;
        const { isI18nReady } = this.props;

        return isDesignTokenApplied && isI18nReady && this.renderContent();
    }

    componentDidMount() {
        const { accessToken, applicationId, prefixAPIUrl } = this.props;

        if (accessToken) {
            GatewayHttpClient.accessToken = accessToken;
        }

        if (applicationId) {
            GatewayHttpClient.applicationId = applicationId;
        }

        if (prefixAPIUrl) {
            GatewayHttpClient.prefixAPIUrl = prefixAPIUrl;
        }

        this.setContainerElementStyle();
        this.fetchConfig();
        this.applyDesignToken();
    }

    componentWillUnmount() {
        this.restoreContainerElementStyle();
        this.destoryClient();
    }

    renderContent = () => {
        const {
            shouldShowChooseOptionWindow,
            shouldShowErrorPage,
            shouldShowHoopWindow,
        } = this.state;
        const { contextCls } = this.props;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                {shouldShowChooseOptionWindow &&
                    this.renderChooseOptionWindow()}
                {shouldShowHoopWindow && this.renderHoopWindow()}
                {shouldShowErrorPage && this.renderErrorPage()}
            </div>
        );
    };

    renderHoopWindow = () => {
        const { contextCls, ...restProps } = this.props;
        const { chatConfigs } = this.state;
        const { hoop, isLeaveAgentMessageEnabled } = chatConfigs ?? {};

        return (
            <Hoop
                contextCls={`${this.baseCls}__hoop`}
                hoop={hoop}
                isLeaveAgentMessageEnabled={isLeaveAgentMessageEnabled}
                {...restProps}
                {...chatConfigs}
            />
        );
    };

    renderChooseOptionWindow = () => {
        const { contextCls, ...restProps } = this.props;
        const { chatConfigs } = this.state;
        const { ewt } = chatConfigs ?? {};

        return (
            <ChooseOptionWindow
                contextCls={`${this.baseCls}__choose-option-window`}
                ewt={ewt}
                {...restProps}
                {...chatConfigs}
            />
        );
    };

    renderErrorPage = () => {
        const { contextCls, i18n, ...restProps } = this.props;
        const windowTitle = i18n.getTranslation('chatModule.errorPage.title');
        const headText = i18n.getTranslation('chatModule.errorPage.headText');
        const subText = i18n.getTranslation('chatModule.errorPage.subText');

        return (
            <GenericMessage
                contextCls={`${this.baseCls}__generic-message`}
                icon={VARIANTS_ICON.WARNING}
                windowTitle={windowTitle}
                headText={headText}
                subText={subText}
                i18n={i18n}
                {...restProps}
            />
        );
    };

    fetchConfig = async () => {
        const newState = {};

        try {
            const chatConfig = await ChatService.getChatConfig();
            const { isAgentOnline, ...restConfig } = chatConfig;
            newState.chatConfigs = restConfig;

            if (isAgentOnline) {
                newState.shouldShowChooseOptionWindow = true;
            } else {
                newState.shouldShowHoopWindow = true;
            }
        } catch (e) {
            newState.shouldShowErrorPage = true;
        } finally {
            this.setState(newState);
        }
    };

    applyDesignToken = async () => {
        try {
            const { brand } = this.props;
            await CssImport.import(brand);
            this.setState({
                isDesignTokenApplied: true,
            });
        } catch (e) {
            console.debug(e.message);
        }
    };

    setContainerElementStyle = () => {
        const { containerElement } = this.props;

        if (containerElement) {
            this.setState({
                originContainerElementPosition: containerElement.style.position,
            });
            containerElement.style.position = 'relative';
        }
    };

    restoreContainerElementStyle = () => {
        const { containerElement } = this.props;
        const { originContainerElementPosition } = this.state;

        if (containerElement && originContainerElementPosition) {
            containerElement.style.position = originContainerElementPosition;
        }
    };

    destoryClient = async () => {
        await TwilioService.destoryClient();
    };

    baseCls = 'web-chat-module';
}

export default withTranslation(ChatModule);
